.container-search{
    display: grid;
    grid-auto-flow: dense;
    justify-content: center;
    margin-top: 1%;
}

.container-search input{
    width: 300px;
    height: 40px;
    background: white;
    border: none;
    font-size: 10pt;
    float: left;
    color: #63717f;
    padding-left: 40px;
    border-radius: 5px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.2), 0 4px 15px 0 rgba(0, 0, 0, 0.19);
    background-image: url('https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTS1m_P3215M6LZ5wHAgYLEJ4zFJj_XEO6gYzWdhhSPmIILd-wuxgfv3Y_sUNHlm_gxRUs&usqp=CAU');
    background-position: 10px 10px; 
    background-repeat: no-repeat;
    background-size: 20px;
}
.form-create{
    display: flex;
    justify-content: center;
    margin-bottom: 80px;
    margin-top: 60px;
    font-family: 'Montserrat', sans-serif;
}
 
.form-create div{
    margin-bottom: 30px;
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
}

.form-create input{
    border-radius: 5px;
    border: none;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.2), 0 4px 15px 0 rgba(0, 0, 0, 0.19);
    padding: 5px;
}

.danger{
    color: red;
    font-size: 12px;
}

.btn-submit{
    color:  #7b7bc2;
    background-color:white;
    border-bottom: 1px solid transparent;
    border-top: 1px solid transparent;
    border-left: 1px solid transparent;
    border-right: 1px solid transparent;
    border-radius: 10px;
    border: solid 1px;
    cursor: pointer;
    padding: 6px;
    margin-left: 260px;
}

.btn-submit:hover{
    background-color:  #bcbdf1;
    color:  #2E2E48;
}

.brand-s select, .categ-s select{
    border-radius: 5px;
    border: none;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.2), 0 4px 15px 0 rgba(0, 0, 0, 0.19);
    padding: 4px;
    width:150px;
    background-color: #ffffff;
    color:#2E2E48;
}
  
.brand-s select option:hover, .categ-s select option:hover{
    padding: 10px;
}

.categ-btn{
    max-width: 300px;
}

.categ-btn button{
    color:  #7b7bc2;
    background-color:white;
    border-bottom: 1px solid transparent;
    border-top: 1px solid transparent;
    border-left: 1px solid transparent;
    border-right: 1px solid transparent;
    border-radius: 10px;
    border: solid 1px;
    cursor: pointer;
    padding: 6px;   
}
.pageNumbers{
    list-style: none;
    font-family: 'Montserrat', sans-serif;
    color: #2E2E48;
    margin: 0 auto;
    margin-top: 6vh;
    display: flex;
    justify-content: space-around;
    width: 50vh;    
}

.pageNumbers button{
    color: #bcbdf1;
    background-color: #2E2E48;
    border-bottom: 1px solid transparent;
    border-top: 1px solid transparent;
    border-left: 1px solid transparent;
    border-right: 1px solid transparent;
    border-radius: 3px;
    cursor: pointer;
    margin-bottom: 30px;
}

.pageNumbers li {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-left: 10px;
    margin-right: 10px;
    border-radius: 15px;
    cursor: pointer;
  }
  
  .pageNumbers li.active {
      font-weight: 600;
    color: #2E2E48;
  }
  
  .pageNumbers li button {
    background-color: transparent;
    border: none;
    color: black;
    font-size: 1.5rem;
    cursor: pointer;
  }
  .pageNumbers li button:hover {
    background-color: black;
    color: white;
  }
  .pageNumbers li button:focus {
    outline: none;
  }

.imgproducts {  
    max-width: 100%;  
    height: 320px;
    /* -webkit-box-shadow:  4px 4px 3px rgba(84,85,100,0.4); 
    box-shadow:  4px 4px 3px rgba(84,85,100,0.151);
     */
    border-radius: 5px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.2), 0 4px 15px 0 rgba(0, 0, 0, 0.19); 
    width: 270px;
    border-radius: 20px;
    /* border: 0.1px solid rgba(84, 85, 100, 0.151); */
}

.imgproducts img {  
    max-width: 90%;  
    height: auto; 
    width: auto; 
    background-color: rgb(255, 255, 255);
    border-radius: 8px;
}

.sheetGrid {
    display: grid;
    grid-gap: 5rem 1rem;
    gap: 5rem 1rem;
    grid-auto-flow: dense;
    grid-template-rows: 18rem 18rem /* 18rem */;
    grid-template-columns: repeat(auto-fill, minmax(17rem, 17rem));
    justify-content: space-around;
    text-align: center;
    margin-left: 40px;
    margin-right: 40px;
    margin-bottom: 50px;
    font-family: 'Montserrat', sans-serif;
    color: rgb(29, 41, 109);
}


.filters{
    margin: 0 auto;
    margin-top: 5vh;
    margin-bottom: 3vh;
    display: flex;
    justify-content: space-around;
    width: 30vh;
}

.filters button{
    color:  #7b7bc2;
    background-color:white;
    border-bottom: 1px solid transparent;
    border-top: 1px solid transparent;
    border-left: 1px solid transparent;
    border-right: 1px solid transparent;
    border-radius: 10px;
    border: solid 1px;
    cursor: pointer;   
}

.filters button:hover,
.filters button.active {
    background-color:  #bcbdf1;
    color:  #2E2E48;
}

.filters select{
    background-color: #ffffff;
    color:#2E2E48;
    border: 1px solid #7b7bc2;
    border-radius: 9px;
  }
  
  .filters select option {
    padding: 10px;
  }


  .name{
    margin:10px ;
  }

