@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

.nav-container{
    /* display: grid;
    grid-template-columns: 0.5fr 0.5fr 0.2fr;
    background-color: rgb(21, 21, 75); */
    height: 50px;
    background-color: #2E2E48 ;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'Montserrat', sans-serif;   
    font-size: 15px; 
}

.link-home button {
    color: white;
    background-color: #2E2E48;
    height: 100%;
    padding: 6px 10px;
    border-bottom: 1px solid transparent;
    border-top: 1px solid transparent;
    border-left: 1px solid transparent;
    border-right: 1px solid transparent;
    border-radius: 3px;
    font-family: 'Montserrat', sans-serif;   
    margin-left: 20px;
}

.link-home button:hover,
.link-home button.active {
    background-color:  #bcbdf1;
    color:  #2E2E48;
}

.links-compra a {
    color: white;
    height: 100%;
    padding: 6px 5px;
    border-bottom: 4px solid transparent;
    border-radius: 3px;
}

.links-compra a:hover,
.links-compra a.active {
background-color:  #bcbdf1;
color:  #2E2E48;
}

.links-login a {
    color: white;
    height: 100%;
    padding: 6px 5px;
    border-bottom: 4px solid transparent;
    border-radius: 3px;
    margin-right: 20px;
}

.links-login a:hover,
.links-login a.active {
background-color:  #bcbdf1;
color:  #2E2E48;
}

.searchbar {
    padding-right: 15px;
}